<template>
  <div
    id="homePageCarousel"
    @mouseover="stopCarousel"
    @mouseleave="startCarousel"
  >
    <div class="carouselGroup">
      <!-- 白 -->
      <div class="carouselItem show">
        <img
          :src="require('../assets/media/homePage1.png')"
          alt="homePageimg1"
        />
        <div id="himg1">
          <p>全球領先的智慧對講方案</p>
          <img :src="require('../assets/media/himg1.png')" alt="" />
        </div>
      </div>
      <!-- 黑 -->
      <div class="carouselItem">
        <img
          :src="require('../assets/media/homePage2.png')"
          alt="homePageimg2"
        />
      </div>
      <!-- 黑 -->
      <div class="carouselItem">
        <img
          :src="require('../assets/media/homePage3.png')"
          alt="homePageimg3"
        />
      </div>
      <!-- 黑 -->
      <div class="carouselItem">
        <img
          :src="require('../assets/media/homePage4.png')"
          alt="homePageimg4"
        />
      </div>
      <div class="carouselItem">
        <img
          :src="require('../assets/media/homePage5.png')"
          alt="homePageimg5"
        />
      </div>
    </div>
    <div class="carouselBtn">
      <div>
        <img
          @click="showPre"
          @mouseover="stopCarousel"
          @mouseleave="startCarousel"
          :src="require('../assets/media/leftBtn.png')"
        />
      </div>
      <div>
        <img
          @click="showNext"
          @mouseover="stopCarousel"
          @mouseleave="startCarousel"
          :src="require('../assets/media/rightBtn.png')"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      counter: 0,
      timer: null,
    };
  },
  mounted() {
    // 輪播初始化
    clearInterval(this.timer);
    // 以logoSwitch與logoActive區分輪播與Nav hover效果反白
    // 避免首張圖片removeclass，logo顯示黑色bug
    document.querySelector(".logo").classList.add("class", "logoSwitch"),
      document
        .querySelector(".navItem")
        .classList.add("class", "navCarouselSwitch");

    // 輪播
    this.timer = setInterval(() => {
      // 根據輪播圖片改變nav的字體顏色
      this.navCarouselSwitch();
      this.showCurrent();
      this.counter++;
    }, 4000);
  },
  methods: {
    showCurrent() {
      var itemToShow = Math.abs(
        this.counter % document.querySelectorAll(".carouselItem").length
      );
      [].forEach.call(
        document.querySelectorAll(".carouselItem"),
        function (el) {
          el.classList.remove("show");
        }
      );
      document
        .querySelectorAll(".carouselItem")
        [itemToShow].classList.add("show");
    },
    showNext() {
      //
      this.counter++;
      this.showCurrent();
      this.navCarouselSwitch();
    },
    showPre() {
      this.counter--;
      if (this.counter <= 0) {
        this.counter = document.querySelectorAll(".carouselItem").length;
      }
      this.showCurrent();
      this.navCarouselSwitch();
    },
    startCarousel() {
      this.timer = setInterval(() => {
        this.counter++;
        this.navCarouselSwitch();
        this.showCurrent();
      }, 4000);
    },
    stopCarousel() {
      clearInterval(this.timer);
    },
    // 根據輪播圖修改nav顏色
    navCarouselSwitch() {
      this.counter % document.querySelectorAll(".carouselItem").length == 0
        ? // 判斷輪播為第一張圖時改logo、字體為白色的class
          // 非第一張圖時加上黑色logo、字體class
          (document.querySelector(".logo").classList.add("class", "logoSwitch"),
          document
            .querySelector(".navItem")
            .classList.add("class", "navCarouselSwitch"))
        : (document.querySelector(".logo").classList.remove("logoSwitch"),
          document
            .querySelector(".navItem")
            .classList.remove("navCarouselSwitch"));
    },
  },
  beforeUnmount() {
    clearInterval(this.timer);
    this.counter = 0;
    document.querySelector(".logo").classList.remove("logoSwitch"),
      document.querySelector(".navItem").classList.remove("navCarouselSwitch");
  },
};
</script>
